body {
  font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif;
}

* {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

/* ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
} */
a {
  text-decoration: none;
}
img {
  display: block;
  width: 100%;
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  height: 1px;
  width: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1140px;
  padding: 15px;
  height: auto;
  margin: 0 auto;
  margin-top: 15px;
  color: #fff;
  background-image: url(../../images/sajt-holovna-dekstop.jpg);
  background-size: contain;
  border-radius: 4px;
  /* background-repeat: no-repeat; */
  /* object-fit: fill; */
}

.App {
  max-width: 1140px;
}
